.container {
  width: 100%;
  height: 20000px;
  .animationContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .computerContainer {
      position: relative;

      .computer {
        position: absolute;
        width: 10rem;
        max-height: 10rem;
      }
      .screen {
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        top: 0.8rem;
        left: 0.4rem;
        width: 9.2rem;
        height: 5.5rem;
        padding: 0;
        p {
          transform: translate(100px, 100px);
          margin-right: 0.5rem;
          font-family: Monospace;
          color: #14c18b;
        }
      }
    }
  }
}
